import React, { Component } from 'react'
import Barchart from '../../barchart'
import JsonData from '../../../data/teacher/chengang/工作量（教分，当年）.json'
export default class Gongzuoliang extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        工作量（教分，当年）
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart text='陈刚' data={JsonData}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}
