import React, { Component } from 'react'
import Barchart from '../../barchart'
import JsonData from '../../../data/teacher/linyin/科研分（当年）.json'
export default class Keyanfen extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        科研分（当年）
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart text='林崟' data={JsonData}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}
